.visuallyHidden:not(:focus):not(:active) {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(00 0 0);
	overflow: hidden;
}

/*****GENERAL STYLES */
html {
	font-size: 62.5%;
	scroll-behavior: smooth;
}

body {
	background-color: #fffaf1;
	font-size: 1.6rem;
	font-family: 'Roboto', sans-serif;
}

textarea {
	font-family: 'Roboto', sans-serif;
	resize: none;
}

img {
	width: 100%;
	height: auto;
	display: block;
}

.wrapper {
	width: 90%;
	max-width: 1150px;
	margin: 0 auto;
}

/*******GENERAL FONT STYLING */
h1 {
	font-family: 'Barrio', cursive;
	font-weight: bold;
	font-size: clamp(5.5rem, 5.5vw, 7rem);
	text-align: center;
	padding-bottom: 20px;
	letter-spacing: 0.25rem;
}

p {
	line-height: 1.6;
}

a {
	text-decoration: none;
	color: #3a2e22;
}

input,
textarea {
	text-transform: uppercase;
}

.junoType {
	display: block;
	font-family: 'Roboto', sans-serif;
	font-size: clamp(2.5rem, 2.5vw, 4.5rem);
	text-transform: uppercase;
}

.studentName {
	font-size: clamp(2rem, 2.3vw, 2.5rem);
}

.boldLabel,
button,
footer {
	font-weight: 700;
	text-transform: uppercase;
}

header {
	display: flex;
	flex-direction: column;
	padding: 60px 0px;
	justify-content: center;
	align-items: center;
}

.headerButtons {
	display: flex;
	justify-content: space-between;
	width: 462px;
}

button {
	background-color: lightgray;
	border: 0px solid transparent;
	width: 200px;
	border-radius: 3px;
	padding: 5px 15px;
	margin-top: 10px;
	text-align: center;
}

/*******FORM STYLES */

.formToFill {
	width: 462px;
	margin: 0 auto;
}

form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	padding-bottom: 60px;
}

input,
textarea {
	border: 0px;
	border-bottom: 2px solid grey;
	padding: 10px;
	margin: 10px 0px;
	width: 100%;
}

.standaloneInput input {
	width: 100%;
}

.inputsFlex {
	display: flex;
}

.inputsFlex input {
	width: 50%;
	margin-right: 10px;
}

/******STUDENT CARD DISPLAY SECTION  */
main {
	background: url('./assets/josephine-bredehoft-unsplash.jpg');
	/* credit <span>Photo by <a href="https://unsplash.com/@sibilant?utm_source=unsplash&amp;utm_medium=referral&amp;utm_content=creditCopyText">Josephine Bredehoft</a> on <a href="https://unsplash.com/s/photos/cork-board?utm_source=unsplash&amp;utm_medium=referral&amp;utm_content=creditCopyText">Unsplash</a></span> */
	padding: 60px 0;
	min-height: 70vh;
}

main form {
	padding: 0px;
}

select {
	text-transform: uppercase;
	padding: 10px 30px 10px 20px;
	margin-bottom: 20px;
}

.cardsContainer {
	display: grid;
	grid-template-columns: 33% 33% 33%;
	grid-template-rows: auto;
	margin: 0px auto;
}

.cardsContainer div {
	padding: 30px;
	margin: 10px;
	min-height: 250px;
	background-color: #fffaf1;
	box-shadow: 0px 2px 2px black;
}

.cardsContainer div:nth-child(odd) {
	transform: rotate(-1deg);
}

.cardsContainer div:nth-child(even) {
	transform: rotate(1deg);
}

.fa-github-square,
.fa-linkedin,
.fa-desktop {
	margin-right: 10px;
}

.scroll {
	font-size: 3rem;
	color: white;
	height: 20px;
	width: 30px;
	padding: 10px;
	border-radius: 3px;
	background-color: #3a2e22;
	filter: brightness(1);
}

.scroll:hover {
	filter: brightness(3.5);
}

footer {
	padding: 10px 0px;
}

.footerFlex {
	display: flex;
	justify-content: space-between;
}

@media (max-width: 1024px) {
	.cardsContainer {
		grid-template-columns: 50% 50%;
		grid-template-rows: auto;
	}
}

@media (max-width: 600px) {
	.inputsFlex {
		flex-direction: column;
		width: 100%;
	}

	.inputsFlex input {
		width: 100%;
		margin-right: 0px;
	}

	input,
	textarea {
		padding: 8px;
		margin: 8px 0px;
		width: 100%;
	}

	.headerButtons {
		width: 100%;
		flex-direction: column;
	}

	button {
		width: 100%;
	}

	.formToFill {
		width: 100%;
	}

	.cardsContainer {
		grid-template-columns: 100%;
		grid-template-rows: auto;
	}
}
